"use client";

import React, { useState } from "react";

const AddItemForm = () => {
  const [formData, setFormData] = useState({
    itemName: "",
    location: "",
    barcodeID: "",
    status: "",
    category: "",
    description: "",
    purchaseDate: "",
    purchaseCost: "",
    receipt: null,
  });

  const handleChange = (e) => {
    const { name, value, files } = e.target;
    if (name === "receipt") {
      setFormData({ ...formData, [name]: files[0] });
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };

  const handleSubmit = () => {
    console.log(formData);
    // Perform save logic
  };

  return (
    <div className="p-6 md:p-10">
      <h2 className="text-xl font-bold mb-6 border-l-4 border-red-600 pl-2">
        Add new Item
      </h2>
      <div className="bg-[#B6BC9D] p-6 rounded-md shadow-md space-y-4 max-w-4xl">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block font-medium">Item Name</label>
            <input
              type="text"
              name="itemName"
              value={formData.itemName}
              onChange={handleChange}
              className="w-full p-2 rounded shadow"
            />
          </div>
          <div>
            <label className="block font-medium">Location</label>
            <input
              type="text"
              name="location"
              value={formData.location}
              onChange={handleChange}
              className="w-full p-2 rounded shadow"
            />
          </div>
          <div>
            <label className="block font-medium">Barcode ID</label>
            <input
              type="text"
              name="barcodeID"
              value={formData.barcodeID}
              onChange={handleChange}
              className="w-full p-2 rounded shadow"
            />
          </div>
          <div>
            <label className="block font-medium">Status</label>
            <select
              name="status"
              value={formData.status}
              onChange={handleChange}
              className="w-full p-2 rounded shadow"
            >
              <option>Select Status</option>
              <option>Available</option>
              <option>In Use</option>
              <option>Under Repair</option>
              <option>Disposed</option>
            </select>
          </div>
          <div className="md:col-span-2">
            <label className="block font-medium">Category</label>
            <select
              name="category"
              value={formData.category}
              onChange={handleChange}
              className="w-full p-2 rounded shadow"
            >
              <option>Select Category</option>
              <option>Electronics</option>
              <option>Furniture</option>
              <option>Stationery</option>
              <option>Others</option>
            </select>
          </div>
        </div>

        <div>
          <label className="block font-medium">Description (Optional)</label>
          <textarea
            name="description"
            value={formData.description}
            onChange={handleChange}
            className="w-full p-2 rounded shadow"
          ></textarea>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block font-medium">Purchase Date</label>
            <input
              type="date"
              name="purchaseDate"
              value={formData.purchaseDate}
              onChange={handleChange}
              className="w-full p-2 rounded shadow"
            />
          </div>
          <div>
            <label className="block font-medium">Purchase Cost (INR)</label>
            <input
              type="number"
              name="purchaseCost"
              value={formData.purchaseCost}
              onChange={handleChange}
              className="w-full p-2 rounded shadow"
            />
          </div>
        </div>

        <div>
          <label className="block font-medium">Upload Receipt</label>
          <input
            type="file"
            name="receipt"
            onChange={handleChange}
            className="block mt-1"
          />
        </div>

        <div className="flex justify-end gap-4 pt-4">
          <button className="px-4 py-2 bg-gray-200 rounded shadow hover:bg-gray-300">
            Cancel
          </button>
          <button className="px-4 py-2 bg-blue-600 text-white rounded shadow hover:bg-blue-700">
            Generate QR Code
          </button>
          <button
            onClick={handleSubmit}
            className="px-4 py-2 bg-green-600 text-white rounded shadow hover:bg-green-700"
          >
            Save Item
          </button>
        </div>
      </div>
    </div>
  );
};

export default AddItemForm;
